import store from './../store/';

export const auth = (to: any, from: any, next: any) => {
  const exp = store.state.auth.tokenExp;
  if (store.state.auth.authToken
    && (!exp || (exp && Date.now() < (exp * 1000)))) {
    next();
  } else {
    next('/splash');
  }
};

export const loggedOut = (to: any, from: any, next: any) => {
  const exp = store.state.auth.tokenExp;
  if (store.state.auth.authToken
    && (!exp || (exp && Date.now() < (exp * 1000)))) {
    next('/');
  } else {
    next();
  }
};
