























import { Component, Vue, Watch, Prop } from "vue-property-decorator";
import esgFrontendApi from "../api/esg-frontend.api";

@Component({
  components: {}
})
export default class Lookup extends Vue {
  public searchText: string = "";
  public permIdEntities = {};
  private searchLoading: boolean = false;
  private searchTimeout: number = 0;

  get isLoading() {
    return this.searchLoading;
  }

  async updateSearch() {
    if (this.searchText.length >= 3) {
      this.searchLoading = true;
      const url = process.env.VUE_APP_BACKEND_URL || "";
      const token = this.$store.state.auth.authToken;
      let permIdEntities = await esgFrontendApi.getPermIdEntities(
        this.searchText,
        token,
        url
      );
      this.permIdEntities = permIdEntities.entities.filter((x:any) => x.primaryTicker);
      this.searchLoading = false;
    } else {
      this.permIdEntities = {};
      this.searchLoading = false;
    }
  }

  @Watch("searchText")
  public onPropertyChanged(value: string, oldValue: string) {

    if (this.searchTimeout) {
      clearTimeout(this.searchTimeout);
    }
    this.searchTimeout = setTimeout(() => {
      this.updateSearch();
    }, 300);
  }

  public selectEntity(entity: any) {
    this.$store.commit("setEntity", entity);
    this.permIdEntities = {};
    this.searchText = '';
    this.$router.push({ path: 'funnel' })
  }
}
