export default class NewsStory {
    public storyText: string;

    constructor(data: any) {
        if (data.newsItem.contentSet.inlineXML) {
            this.storyText = data.newsItem.contentSet.inlineXML[0].$;
          } else {
            this.storyText = '<h2>No story body</h2>';
          }
    }
}