



























import { Component, Vue } from "vue-property-decorator";

@Component({
  components: {}
})
export default class Modal extends Vue {
  get showModal() {
    return this.$store.state.modal.modalActive;
  }

  get modalTitle() {
    return this.$store.state.modal.title;
  }

  get modalLoading() {
    return this.$store.state.modal.modalLoading;
  }

  get newsStory() {
    return this.$store.state.news.newsStory;
  }

  private closeModal() {
    this.$store.commit("set", false);
    this.$store.commit("setModalActive", false);
  }
}
