import get from './get';
import post from './post';

export default {
    async getPermIdEntities(search: string, token: string, apiPath: string) {
        try {
            const res = await get(apiPath, '/permid/' + search, token);
            return res.data;
        } catch (err) {
            throw err;
        }
    },
    async getNewsHeadlines(ric: string, token: string, apiPath: string): Promise<any> {
        try {
            const data = JSON.stringify({ric});
            const res = await post(apiPath, '/news', token, data);
            return res;
        } catch (err) {
            throw err;
        }
    },
    async getNewsStory(storyId: string, token: string, apiPath: string): Promise<any> {
        try {
            const data = JSON.stringify({storyId});
            const res = await post(apiPath, '/story', token, data);
            return res;
        } catch (err) {
            throw err;
        }
    },
    async classifyHeadline(headline: string, id: string, token: string, apiPath: string): Promise<any> {
        try {
            const data = JSON.stringify({text: headline, id});
            const res = await post(apiPath, '/classify', token, data);
            return res;
        } catch (err) {
            throw err;
        }
    },
    async archiveNewsStory(archivedStory: any, token: string, apiPath: string): Promise<any> {
        try {
            const data = JSON.stringify(archivedStory);
            const res = await post(apiPath, '/archive', token, data, );
            return res;
        } catch (err) {
            throw err;
        }
    },
};
