import { MutationTree, ActionTree, Module } from 'vuex';
import { RootState, NewsState } from './../types';

const state: NewsState = {
  newsHeadlines: [],
  newsStory: {}
};

const mutations: MutationTree <NewsState> = {
  setNewsHeadlines(newsState, newsHeadlines: []) {
    newsState.newsHeadlines = newsHeadlines;
  },
  setNewsStory(newsState, newsStory: {}) {
    newsState.newsStory = newsStory;
  }
};

const actions: ActionTree <NewsState, RootState> = {
};

export const news: Module<NewsState, RootState> = {
  state,
  mutations,
  actions
};
