


























































import { Component, Vue } from "vue-property-decorator";
import esgFrontendApi from "../api/esg-frontend.api";
import NewsService from "../services/NewsService";
import NewsHeadline from "../models/newsHeadline";
import ArchivingService from "../services/ArchivingService";

@Component({
  components: {}
})
export default class Funnel extends Vue {
  loading: boolean = false;

  get newsHeadLines() {
    return this.$store.state.news.newsHeadlines.filter((x: any) => x.archived === false);
  }

  get entity() {
    return this.$store.state.entities.entity;
  }

  archiveNewsEntities() {
    let selectedNewsHeadlines = this.newsHeadLines.filter((x: any) => x.selected === true);
    const url = process.env.VUE_APP_BACKEND_URL || "";
    const token = this.$store.state.auth.authToken;
    ArchivingService.ArchiveNewsEntities(selectedNewsHeadlines, token, url);
  }

  async getNewsItem(newsHeadline: NewsHeadline) {
    this.$store.commit("setModalActive", true);
    this.$store.commit("setModalTitle", newsHeadline.headline);
    this.$store.commit("setModalLoading", true);
    const url = process.env.VUE_APP_BACKEND_URL || "";
    const token = this.$store.state.auth.authToken;
    const data = await esgFrontendApi.getNewsStory(newsHeadline.id, token, url);
    NewsService.ProcessStory(data);
    this.$store.commit("setModalLoading", false);
  }

  async mounted() {
    this.loading = true;
    const url = process.env.VUE_APP_BACKEND_URL || "";
    const token = this.$store.state.auth.authToken;
    // Get news articles for selected entity
    if (this.entity.primaryTicker) {
      const data = await esgFrontendApi.getNewsHeadlines(
        this.entity.primaryTicker,
        token,
        url
      );
      await NewsService.ProcessHeadlines(data, token, url);
    } else {
      alert("No RIC");
    }
    this.loading = false;
  }
}
