import NewsHeadline from '@/models/newsHeadline';
import NewsStory from '@/models/newsStory';
import store from './../store/';
import esgFrontendApi from '@/api/esg-frontend.api';

export default class NewsService {
    public static async ProcessHeadlines(headlineData: any, token: string, apiPath: string) {
        const newsHeadlines = [];
        for (const data of headlineData) {
          const newsHeadline = new NewsHeadline(data);
          // tslint:disable-next-line:max-line-length
          const classification = await esgFrontendApi.classifyHeadline(newsHeadline.headline,newsHeadline.id, token, apiPath);
          newsHeadline.classification = classification.prediction;
          newsHeadline.confidence = classification.prediction_prob;
          newsHeadlines.push(newsHeadline);
        }
        store.commit('setNewsHeadlines', newsHeadlines);
    }

    public static ProcessStory(data: any) {
      const newsStory = new NewsStory(data);
      store.commit('setNewsStory', newsStory);
    }
}