import { MutationTree, ActionTree, Module } from 'vuex';
import { RootState, AuthState } from './../types';

const state: AuthState = {
  authToken: localStorage.getItem('token') || '',
  tokenExp: localStorage.getItem('tokenExp') || 0
};

const mutations: MutationTree <AuthState> = {
  setAuthToken(authState, authToken: string) {
    authState.authToken = authToken;
  },
  setTokenExp(authState, tokenExp: number) {
    authState.tokenExp = tokenExp;
  }
};

const actions: ActionTree <AuthState, RootState> = {
  onLogin({commit}, { token, exp }) {
    localStorage.setItem('token', token);
    if (exp) {
      localStorage.setItem('tokenExp', exp);
    } else {
      localStorage.removeItem('tokenExp');
    }
    
    commit('setAuthToken', token);
    commit('setTokenExp', exp);
  },
  onLogout({commit}) {
    localStorage.removeItem('token');
    localStorage.removeItem('tokenExp');
    commit('setAuthToken', '');
    commit('setTokenExp', 0);
  }
};

export const auth: Module<AuthState, RootState> = {
  state,
  mutations,
  actions
};
