import { MutationTree, ActionTree, Module } from 'vuex';
import { RootState, EntitiesState } from './../types';

const state: EntitiesState = {
  entity: {}
};

const mutations: MutationTree <EntitiesState> = {
  setEntity(entitiesState, entity: string) {
    entitiesState.entity = entity;
  },
};

const actions: ActionTree <EntitiesState, RootState> = {
};

export const entities: Module<EntitiesState, RootState> = {
  state,
  mutations,
  actions
};
