








import { Component, Vue, Prop } from 'vue-property-decorator';
declare var AuthBar: any;

@Component({
  components: {
  },
})
export default class Auth extends Vue {
  
  get isTransparent() {
    return this.$route.name === 'splash' || this.$route.name === 'home';
  }

  get authToken() {
    return this.$store.state.auth.authToken;
  }

  get tokenExp() {
    return this.$store.state.auth.tokenExp;
  }
  
  /**
   * Sets token and token expiry we've got from auth bar.
   * NB: doesn't redirect to eg '/', because this is called every
   * time the auth bar is initialised, ie even if a logged in user
   * refreshes the page. Route handling from logged out -> logged 
   * in is handled in individual views, eg Splash.vue
   */
  private onLogin(tokens: any[]) {
    const siteTokens = tokens.find((t: any) => t.dst === process.env.VUE_APP_ISSUER);
    this.$store.dispatch('onLogin', {
      token: siteTokens.token,
      exp: siteTokens.exp || 0
    });
    this.listenForFocus();
  }

  private onLogout() {
    this.$store.dispatch('onLogout');
    this.$router.push('/splash');
  }

  private addAuthBar() {
    AuthBar.render(
      {
        identServiceUrl: process.env.VUE_APP_IDENTITY_SERVICE,
        issuer: process.env.VUE_APP_ISSUER,
        services: [process.env.VUE_APP_ISSUER],
        providers: ['safe'],
        redirectOnLogout: false,
        logoImageUrl:
          'https://s3-eu-west-1.amazonaws.com/auth-lib/assets/refinitiv_labs_logo_cropped.svg',
        logoUrl: '/'
      },
      '#authBarPlaceholder'
    );
    AuthBar.on('login', (tokens: any[]) => {
      this.onLogin(tokens);
    });
    AuthBar.on('logout', () => {
      this.onLogout();
    });
    AuthBar.on('expired', (token: any) => {
      this.onLogout();
    });
  }

  /** 
   * We need the following because...
   * To catch expired tokens, the authlib code has a timeout based on the 
   * token expiry time. On completion of this timeout, the expired event is
   * fired. We do listen for this (see above .on("expired")), but in Chrome inactive tabs
   * kill long running timeouts/intervals. We therefore also want to check
   * for token expiry whenever the page is re-focussed.
   */
  private listenForFocus() {
    document.addEventListener('visibilitychange', () => {
      if (document.visibilityState === 'visible' && this.tokenExp) {
        if (Date.now() > (this.tokenExp * 1000)) {
          this.onLogout();
        }
      }
    });
  }

  private mounted() {
    this.addAuthBar();
  }
}

